*,
*::before,
*::after {
  box-sizing: border-box;
}

.page__body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  max-width: 320px;
  padding: 0;
  margin: 0 auto;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 400;
}
